import PropTypes from 'prop-types';

// material-ui
import { Box, Card, Grid, Stack, Typography } from '@mui/material';

// project import
import ComponentSkeleton from './ComponentSkeleton';
import { createDispatchHook, useDispatch, useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import { DataGrid } from '@mui/x-data-grid';
import { Upload } from '../../../node_modules/antd/lib/index';
import { FormGroup } from 'react-bootstrap';
import { PlusOutlined } from '@ant-design/icons';
import CircularProgress from '@mui/material/CircularProgress';
import {
    Button,
    Divider,
    FormControl,
    InputAdornment,
    InputLabel,
    MenuItem,
    Modal,
    OutlinedInput,
    Select,
    TextField
} from '../../../node_modules/@mui/material/index';
import { CKEditor } from 'ckeditor4-react';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { notification, Space, Popconfirm } from 'antd';
import { Tabs } from 'antd';
import formatThousand from 'util/formatThousans';
import { useNavigate } from 'react-router-dom';
import { deleteProductById, getProducts } from 'actions/product';
import { getAllCategory } from 'actions/category';

// ===============================|| COLOR BOX ||=============================== //
const { TabPane } = Tabs;
function ColorBox({ bgcolor, title, data, dark, main }) {
    return (
        <>
            <Card sx={{ '&.MuiPaper-root': { borderRadius: '0px' } }}>
                <Box
                    sx={{
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        py: 2.5,
                        bgcolor,
                        color: dark ? 'grey.800' : '#ffffff',
                        border: main ? '1px dashed' : '1px solid transparent'
                    }}
                >
                    {title && (
                        <Grid container justifyContent="space-around" alignItems="center">
                            <Grid item>
                                {data && (
                                    <Stack spacing={0.75} alignItems="center">
                                        <Typography variant="subtitle2">{data.label}</Typography>
                                        <Typography variant="subtitle1">{data.color}</Typography>
                                    </Stack>
                                )}
                            </Grid>
                            <Grid item>
                                <Typography variant="subtitle1" color="inherit">
                                    {title}
                                </Typography>
                            </Grid>
                        </Grid>
                    )}
                </Box>
            </Card>
        </>
    );
}

ColorBox.propTypes = {
    bgcolor: PropTypes.string,
    title: PropTypes.string,
    data: PropTypes.object.isRequired,
    dark: PropTypes.bool,
    main: PropTypes.bool
};

// ===============================|| COMPONENT - COLOR ||=============================== //

const ComponentColor = () => {
    let navigate = useNavigate();
    const dispatch = useDispatch();
    const product = useSelector((state) => state.product);
    const [selectedRows, setSelectedRows] = useState([]);
    const [open, setOpen] = useState(false);
    const [type, setType] = useState('');
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const category = useSelector((state) => state.category);
    const auth = useSelector((state) => state.auth);
    const [productPicture, setProductPicture] = useState([]);
    useEffect(() => {
        dispatch(getProducts());
        dispatch(getAllCategory());
    }, []);
    // useEffect(() => {
    //     if (!auth.token) {
    //         navigate('/login');
    //         notification['error']({
    //             message: 'Đăng nhập',
    //             description: 'Vui lòng đăng nhập để sử dụng hệ thống.'
    //         });
    //     }
    // }, []);
    useEffect(() => {
        if (selectedRows[0]) {
            selectedRows[0] && selectedRows[0].productPicture.map((item) => Object.assign(item, { url: item.img }));
        }

        setProductPicture(selectedRows[0] ? selectedRows[0].productPicture : []);
    }, [selectedRows]);

    // useEffect(() => {
    //     if (product.products) {
    //         product.products.map((item, index) => (item.id = index + 1));
    //     }
    // }, [dispatch]);
    const text = 'Bạn có chắc chắn muốn xoá?';
    const columns = [
        { field: 'id', headerName: 'Mã sản phẩm', width: 100 },
        { field: 'name', headerName: 'Tên sản phẩm', width: 300 },
        { field: 'category.name', headerName: 'Hãng', width: 130 },
        { field: 'salePrice', headerName: 'Giá tiền', width: 130 },
        {
            field: 'quantity',
            headerName: 'Số lượng',
            type: 'number',
            width: 90
        },
        {
            field: 'description',
            headerName: 'Mô tả',
            description: 'This column has a value getter and is not sortable.',
            sortable: false,
            width: 160
            // valueGetter: (params) => `${params.row.firstName || ''} ${params.row.lastName || ''}`
        }
    ];
    // eslint-disable-next-line
    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 1000,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4
    };

    notification.config({
        placement: 'topRight',
        top: 80,
        duration: 3,
        rtl: false
    });
    const handleView = () => {
        if (selectedRows.length === 0) {
            notification['warning']({
                message: 'Xem sản phẩm',
                description: 'Vui lòng chọn sản phẩm bạn muốn xem.'
            });
        } else if (selectedRows.length >= 2) {
            notification['warning']({
                message: 'Xem sản phẩm',
                description: 'Vui lòng chỉ chọn một sản phẩm.'
            });
        } else {
            setType('view');
            handleOpen();
        }
    };
    const handleDeleteProduct = () => {
        console.log(selectedRows);
    };
    const confirm = () => {
        if (selectedRows.length === 0) {
            notification['warning']({
                message: 'Xoá sản phẩm',
                description: 'Vui lòng chọn sản phẩm bạn muốn xoá.'
            });
        } else {
            let listIdProduct = [];
            selectedRows.map((item) => {
                listIdProduct.push(item._id);
            });
            const payload = {
                productId: listIdProduct
            };
            dispatch(deleteProductById(payload));
            if (product.deleteMessage === 'success') {
                notification['success']({
                    message: 'Xoá sản phẩm',
                    description: 'Xoá sản phẩm thành công.'
                });
            }
            if (product.deleteMessage === 'error') {
                notification['error']({
                    message: 'Xoá sản phẩm',
                    description: 'Xoá sản phẩm không thành công.'
                });
            }
        }
    };
    const handleEditProduct = () => {
        if (selectedRows.length === 0) {
            notification['warning']({
                message: 'Chỉnh sửa sản phẩm',
                description: 'Vui lòng chọn sản phẩm bạn muốn chỉnh sửa.'
            });
        } else if (selectedRows.length >= 2) {
            notification['warning']({
                message: 'Xem sản phẩm',
                description: 'Vui lòng chỉ chọn một sản phẩm.'
            });
        } else {
            // notification['success']({
            //     message: 'Chỉnh sửa sản phẩm',
            //     description: 'Coming Soon'
            // });
            setType('edit');
            handleOpen();
        }
    };
    const createCategoryList = (categories, options = []) => {
        for (let category of categories) {
            options.push({ value: category._id, name: category.name });
            if (category.children.length > 0) {
                createCategoryList(category.children, options);
            }
        }
        return options;
    };
    const uploadButton = (
        <div>
            <PlusOutlined />
            <div
                style={{
                    marginTop: 8
                }}
            >
                Upload
            </div>
        </div>
    );
    const modalProduct = (type) => {
        let title;
        let disable;
        if (type === 'edit') {
            title = 'Chỉnh sửa sản phẩm';
            disable = false;
        } else {
            title = 'Xem chi tiết sản phẩm';
            disable = true;
        }
        return (
            <Modal open={open} onClose={handleClose} aria-labelledby="modal-modal-title" aria-describedby="modal-modal-description">
                <Box sx={style}>
                    <Typography id="modal-modal-title" variant="h3" component="h2">
                        {title}
                    </Typography>
                    <Tabs defaultActiveKey="1" style={{ color: 'black', fontSize: '19px' }}>
                        <TabPane tab={<span>Thông tin chung</span>} key="1">
                            <div
                                className="container_addProduct"
                                style={{
                                    display: 'flex',
                                    paddingTop: '0px',
                                    color: 'black',
                                    fontSize: '17px'
                                }}
                            >
                                <div
                                    className="container_form_addProduct"
                                    style={{
                                        paddingBottom: '50px',
                                        width: '40%',
                                        paddingRight: '30px'
                                    }}
                                >
                                    <TextField
                                        required
                                        style={{ width: '100%', marginBottom: '15px' }}
                                        id="outlined-error"
                                        label="Tên sản phẩm"
                                        value={selectedRows[0] ? selectedRows[0].name : ''}
                                        disabled={disable}
                                    />
                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Số lượng"
                                        type="number"
                                        style={{ width: '100%', marginBottom: '15px' }}
                                        value={selectedRows[0] ? selectedRows[0].quantity : ''}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        disabled={disable}
                                    />

                                    <FormControl fullWidth style={{ width: '100%', marginBottom: '15px' }}>
                                        <InputLabel htmlFor="outlined-adornment-amount">Giá tiền gốc</InputLabel>
                                        <OutlinedInput
                                            id="outlined-adornment-amount"
                                            placeholder={formatThousand(selectedRows[0] ? selectedRows[0].regularPrice : '')}
                                            value={selectedRows[0] ? formatThousand(selectedRows[0].regularPrice) : ''}
                                            startAdornment={<InputAdornment position="start">VNĐ</InputAdornment>}
                                            label="Giá tiền gốc"
                                            disabled={disable}
                                            // InputProps={{
                                            //     inputComponent: NumberFormatCustom
                                            // }}
                                        />
                                    </FormControl>
                                    <FormControl fullWidth style={{ width: '100%', marginBottom: '15px' }}>
                                        <InputLabel htmlFor="outlined-adornment-amount">Giá tiền giảm giá</InputLabel>
                                        <OutlinedInput
                                            id="outlined-adornment-amount"
                                            value={selectedRows[0] ? formatThousand(selectedRows[0].salePrice) : ''}
                                            disabled={disable}
                                            startAdornment={<InputAdornment position="start">VNĐ</InputAdornment>}
                                            label="Giá tiền giảm giá"
                                        />
                                    </FormControl>

                                    <FormControl fullWidth sx={{ height: 100 }}>
                                        <InputLabel id="demo-simple-select-label">Thương hiệu</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="demo-simple-select"
                                            value={selectedRows[0] ? selectedRows[0].categoryId : ''}
                                            label="Thương hiệu"
                                            onChange={(e) => setCategoryId(e.target.value)}
                                        >
                                            {createCategoryList(category.categories).map((option) => (
                                                <MenuItem value={option.value}>{option.name}</MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>

                                    <ul></ul>
                                    <Upload
                                        listType="picture-card"
                                        defaultFileList={productPicture ? productPicture : []}
                                        // onPreview={handlePreview}
                                        // onChange={handleChange}
                                        beforeUpload={() => {
                                            /* update state here */
                                            return false;
                                        }}
                                        disabled={disable}
                                    >
                                        {uploadButton}
                                    </Upload>
                                    {/* <Modal visible={previewVisible} title={previewTitle} footer={null} onCancel={handleCancel}>
                            <img
                                alt="example"
                                style={{
                                    width: '100%'
                                }}
                                src={previewImage}
                            />
                        </Modal> */}
                                    {/* {productPicture.length > 0 ? productPicture.map((pic, index) => <div key={index}>{pic.name}</div>) : null}
                    <input type="file" name="productPicture" onChange={handleProductPictures} /> */}
                                </div>
                            </div>
                        </TabPane>
                        <TabPane tab={<span>Mô tả sản phẩm</span>} style={{ paddingBottom: '20px' }} key="2">
                            <FormGroup>
                                <CKEditor
                                    config={{
                                        enterMode: 2,
                                        resize_minWidth: '100%',
                                        resize_maxHeight: 600,
                                        //filebrowserBrowseUrl: '/browser/browse.php',
                                        //filebrowserUploadUrl: '/uploader/upload.php',
                                        pasteFromWordRemoveStyles: false,
                                        pasteFromWordNumberedHeadingToList: true,
                                        pasteFromWordPromptCleanup: true
                                    }}
                                    initData={selectedRows[0] ? selectedRows[0].description : null}
                                />
                            </FormGroup>
                        </TabPane>
                        <TabPane tab={<span>Mô tả chi tiết</span>} key="3">
                            <div className="table_des" style={{ width: '100%', color: 'black', fontSize: '17px' }}>
                                <div style={{ display: 'block', width: '100%' }}>
                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Thời gian bảo hành"
                                        style={{ width: '45%', marginBottom: '15px', marginRight: '20px' }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].baohanh : null}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        disable={disable}
                                    />

                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Series"
                                        style={{ width: '45%', marginBottom: '15px' }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].Series : null}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        disable={disable}
                                    />
                                </div>
                                <div style={{ display: 'block', width: '100%' }}>
                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Màu sắc"
                                        style={{ width: '45%', marginBottom: '15px', marginRight: '20px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].color : null}
                                        disable={disable}
                                    />

                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="CPU"
                                        style={{ width: '45%', marginBottom: '15px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].cpu : null}
                                        disable={disable}
                                    />
                                </div>
                                <div style={{ display: 'block', width: '100%' }}>
                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Card đồ họa"
                                        style={{ width: '45%', marginBottom: '15px', marginRight: '20px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].cardDohoa : null}
                                        disable={disable}
                                    />

                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Ram"
                                        style={{ width: '45%', marginBottom: '15px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].ram : null}
                                        disable={disable}
                                    />
                                </div>
                                <div style={{ display: 'block', width: '100%' }}>
                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Màn hình"
                                        style={{ width: '45%', marginBottom: '15px', marginRight: '20px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].manhinh : null}
                                        disable={disable}
                                    />

                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Ổ cứng"
                                        style={{ width: '45%', marginBottom: '15px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].ocung : null}
                                        disable={disable}
                                    />
                                </div>
                                <div style={{ display: 'block', width: '100%' }}>
                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Hệ điều hành"
                                        style={{ width: '45%', marginBottom: '15px', marginRight: '20px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].hedieuhanh : null}
                                        disable={disable}
                                    />

                                    <TextField
                                        required
                                        id="outlined-number"
                                        label="Khối lượng"
                                        style={{ width: '45%', marginBottom: '15px' }}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        value={selectedRows[0] ? selectedRows[0].descriptionTable[0].khoiluong : null}
                                        disable={disable}
                                    />
                                </div>
                            </div>
                        </TabPane>
                    </Tabs>
                </Box>
            </Modal>
        );
    };
    return (
        <ComponentSkeleton>
            <Stack direction="row" divider={<Divider orientation="vertical" flexItem />} spacing={2} sx={{ marginBottom: '20px' }}>
                <Button variant="outlined" href="/createProduct" color="success" startIcon={<AddIcon />} style={{ cursor: 'pointer' }}>
                    Thêm mới
                </Button>
                <Button variant="outlined" style={{ cursor: 'pointer' }} onClick={handleView}>
                    Xem
                </Button>
                <Button variant="outlined" style={{ cursor: 'pointer' }} onClick={handleEditProduct}>
                    Chỉnh sửa
                </Button>
                <Popconfirm placement="right" title={text} onConfirm={confirm} okText="Đồng ý" cancelText="Không">
                    <Button
                        variant="outlined"
                        color="error"
                        style={{ cursor: 'pointer' }}
                        startIcon={<DeleteIcon />}
                        // onClick={handleDeleteProduct}
                    >
                        Xoá
                    </Button>
                </Popconfirm>
            </Stack>
            {modalProduct(type)};
            <Grid container spacing={3}>
                <div style={{ height: 600, width: '100%' }}>
                    <DataGrid
                        rows={product.products.length !== 0 ? product.products : []}
                        columns={product.products.length !== 0 ? columns : []}
                        pageSize={8}
                        rowsPerPageOptions={[8]}
                        checkboxSelection
                        // onRowClick={(newSelection) => {
                        //     setArraySelect((arraySelect) => [...arraySelect, newSelection]);
                        //     console.log(arraySelect);
                        // }}
                        onSelectionModelChange={(ids) => {
                            const selectedIDs = new Set(ids);
                            const selectedRows = product.products.filter((row) => selectedIDs.has(row.id));

                            setSelectedRows(selectedRows);
                        }}
                        loading={product.loading}
                        // onCellClick={(value) => console.log(value)}
                    />
                </div>
            </Grid>
        </ComponentSkeleton>
    );
};

export default ComponentColor;
